:root {
  --white: #ffffff;
  --dark: #00171f;
  --navy-blue: #003459;
  --dark-blue: #007ea7;
  --blue: #00a8e8;
}

body,
h6,
h5,
h4,
h3,
h2,
h1,
p {
  font-family: 'Mukta Malar', sans-serif;
  margin: 0px;
  padding: 0px;
}

main {
  background-color: var(--dark);
  display: flex;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  padding: 2rem;
  min-height: calc(100vh - 4rem);
}

main > .card {
  width: 95vw;
  max-width: 500px;
  background-color: var(--white);
  padding: 0.5rem;
  border-radius: 5px;
}

.search > form > button {
  width: 5rem;
  background-color: var(--blue);
  border-color: var(--blue);
  border-style: solid;
  border-radius: 2px;
  cursor: pointer;
  transition: all 0.15s linear;
}

.search > form > button:hover {
  background-color: var(--dark-blue);
  color: var(--white);
}

.search > form > input {
  width: calc(100% - 6rem);
  margin-right: 0.5rem;
}

.search > form > input:focus {
  outline-color: var(--dark-blue);
}

.detail > img {
  border-radius: 50%;
  width: 10rem;
  margin: 0.5rem;
  outline: 2px solid var(--blue);
  outline-offset: 2px;
}

.section-right {
  display: inline-block;
  margin: 2.3rem 0.5rem 0px; /* top | horizontal (left, right) | bottom */
  vertical-align: top;
}

.buttons {
  display: flex;
  justify-content: space-between;
}

.buttons > button {
  width: 10rem;
  padding: 0.5rem 0px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.15s linear;
  border-radius: 2px;
}

.buttons > button > span {
  display: block;
  font-weight: bold;
}

.buttons > button:hover {
  background-color: var(--dark-blue);
  color: var(--white);
}

.detail a {
  color: var(--blue);
  text-decoration: none;
}

.detail a:visited {
  color: var(--navy-blue);
}

.incorrect {
  color: red;
}

.incorrect-input {
  outline-color: red !important;
  border-color: red !important;
  border-style: solid;
}

.active {
  outline: 3px solid var(--blue) !important;
}

.following-list img,
.followers-list img {
  margin-left: 1rem;
  margin-top: 0.5rem;
  width: 2rem;
  border-radius: 50%;
}

.repo-list th {
  text-align: left;
}

.load-more {
  margin-top: -2.5rem;
}

.load-more button {
  width: 100%;
  padding: 5px 0px;
  background-color: transparent;
  border: 2px solid var(--blue);
  cursor: pointer;
  transition: all 0.15s linear;
  border-radius: 2px;
}

.load-more button:hover {
  background-color: var(--dark-blue);
  color: var(--white);
}

.footer {
  text-align: center;
}
.repos {
  background-color: #003459;
  color: #ffffff;
  width: 100%;
}
th {
  background-color: #003459;
}
.th1 {
  background-color: #d0d9e0;
  padding: 10px;
  width: 100%;
}
tbody {
  background-color: #bad9e3;
}
td {
  background-color: #42e5da;
  padding: 10px;
  color: rgb(25, 21, 21);
}
a {
  color: #000000;
  font-size: 20px;
  font-weight: bold;
  justify-content: center;
  text-decoration: none;
}

th,
td {
  border-radius: 10px;
}
